.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home__image{
    width: 100%;
    height: 400px;
    margin-bottom: -50px;
    z-index: -1;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
}
.home__row{
    display: flex;
    margin-left: 5px;
    margin-right: 5px;
    z-index: 1;
    justify-content: space-around;
}
.home__container {
    width: 100%;
}